<template>
  <v-card>
    <v-card-text>
      <v-row>
        <v-col cols="4">

          <mrc_ess_multiText :label="$t('label_The_name')+'*'"
                             :readonly="disabled"
                             :isNational="judegNational('preferred_name')"
                             :error-messages="$errors('stafflist','preferred_name',requiredRule)"
                             v-model="stafflist.preferred_name">
          </mrc_ess_multiText>
        </v-col>
        <v-col cols="4">

          <mrc_ess_multiText :label="$t('label_Surname')+'*'"
                             :readonly="disabled"
                             :isNational="judegNational('family_name')"
                             :error-messages="$errors('stafflist','family_name',requiredRule)"
                             v-model="stafflist.family_name">
          </mrc_ess_multiText>
        </v-col>
        <v-col cols="4">

          <mrc_ess_multiText :label="$t('label_Nickname')"
                             :isNational="judegNational('child_name')"
                             :readonly="disabled"
                             v-model="stafflist.child_name">
          </mrc_ess_multiText>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="12">

          <mrc_ess_multiText :label="$t('label_Show_the_name')+'*'"
                             :isNational="judegNational('show_name')"
                             :error-messages="$errors('stafflist','show_name',requiredRule)"
                             :readonly="disabled"
                             v-model="stafflist.show_name">
          </mrc_ess_multiText>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="12">
          <!-- <v-file-input v-model="files"
                        placeholder="Upload your documents"
                        label="File input"
                        multiple
                        prepend-icon="mdi-paperclip">
            <template v-slot:selection="{ text }">
              <v-chip small
                      label
                      color="primary">
                {{ text }}
              </v-chip>
            </template>
          </v-file-input> -->
          <mrc_ess_brandupload v-model="files2"
                               @changeimgSrc='changeimgSrc'></mrc_ess_brandupload>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="12">

          <mrc_ess_multiText :label="$t('label_Mailbox')+'*'"
                             :isNational="judegNational('staff_email')"
                             :error-messages="$errors('stafflist','staff_email',[...requiredRule,...emailRule])"
                             :readonly="disabled"
                             v-model="stafflist.staff_email">
          </mrc_ess_multiText>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="12">

          <mrc_ess_multiText :label="$t('label_Employee_ID')+'*'"
                             :isNational="judegNational('staff_code')"
                             :error-messages="$errors('stafflist','staff_code',requiredRule)"
                             :readonly="disabled"
                             v-model="stafflist.staff_code">
          </mrc_ess_multiText>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="12">
          <mrc_ess_prompt :label="$t('label_department')+'*'"
                          :errMsg="$errors('stafflist','dept_uuid',requiredRule)"
                          :rules='false'
                          :disabled='disabled'
                          v-model="stafflist.dept_uuid"
                          :obj="companyName">
          </mrc_ess_prompt>
        </v-col>
      </v-row>

      <v-row>
        <v-col cols="6">
          <mrc_ess_prompt :label="$t('label_Position_Title')+'*'"
                          :errMsg="$errors('stafflist','job_post_uuid',requiredRule)"
                          :rules='false'
                          :disabled='disabled'
                          v-model="stafflist.job_post_uuid"
                          :obj="job_post_uuid">
          </mrc_ess_prompt>
        </v-col>
        <v-col cols="6">

          <mrc_ess_prompt :label="$t('label_Post_grades')+'*'"
                          :errMsg="$errors('stafflist','job_post_grade_uuid',requiredRule)"
                          :rules='false'
                          :disabled='disabled'
                          :multiple="true"
                          v-model="job_post_grade_uuid"
                          :obj="mrbase_sys_job_post_grades">
          </mrc_ess_prompt>
        </v-col>
      </v-row>

      <v-row>

        <v-col cols="3">
          <v-checkbox class="mx-2"
                      :true-value="1"
                      :false-value="0"
                      color="#ff8100"
                      v-model="stafflist.is_org_manager"
                      :label="$t('label_The_person_in_charge_of_the_unit')"></v-checkbox>
        </v-col>
        <v-col cols="3">
          <v-checkbox class="mx-2"
                      :true-value="1"
                      :false-value="0"
                      v-model="stafflist.is_dept_manage"
                      color="#ff8100"
                      :label="$t('label_Department_head')"></v-checkbox>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="12">
          <mrc_ess_prompt :label="$t('label_Boss')"
                          :rules='false'
                          :disabled='disabled'
                          v-model="stafflist.direct_leader_code"
                          :obj="superior">
          </mrc_ess_prompt>
        </v-col>
      </v-row>

      <v-row>
        <v-col cols="12">

          <mrc_ess_prompt :label="$t('label_role')+'*'"
                          :rules='false'
                          :errMsg="$errors('stafflist','role_uuids',requiredRule)"
                          multiple
                          :disabled='disabled'
                          v-model="roleUuids"
                          :obj="spica">
          </mrc_ess_prompt>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="6">
          <mrc_ess_datePicker :disabled='false'
                              :errMsg="$errors('stafflist','first_employment_date',requiredRule.concat([{rule:'beforeDate',msg:'不能超过终止日期'}]))"
                              :label="$t('label_The_date_of_entry_to_the_company')+'*'"
                              v-model="stafflist.first_employment_date"></mrc_ess_datePicker>
        </v-col>
        <v-col cols="6">
          <mrc_ess_datePicker :disabled='false'
                              :label="$t('label_The_end_date_of_the_trial_period')+'*'"
                              :errMsg="$errors('stafflist','probation_end_date',requiredRule.concat([{rule:'afterDate',msg:'不能晚于开始时间'}]))"
                              v-model="stafflist.probation_end_date"></mrc_ess_datePicker>
        </v-col>
      </v-row>
    </v-card-text>
    <mrc_ess_submitBtn @cancel='cancel'
                       @success='success'
                       :successText="$t('label_The_next_step')"></mrc_ess_submitBtn>
  </v-card>

</template>
<style scoped>
</style> 
<script>  
import { required, maxLength, between, email, url } from 'vuelidate/lib/validators'
export default {
  components: {},
  props: {},
  data () {
    return {
      items: [],
      disabled: false,
      stafflist: {
        job_post_uuid: "",    //所属岗位
        org_level_uuid: "",    //所属公司 
        show_name: "",    //显示名字
        job_post_grade_uuid: "", //岗位等级
        given_name: "",    //名
        child_name: "",    //小名
        christian_name: "",    //员工洋名
        role_uuids: "",    //所属角色
        dept_uuid: "",    //所属部门
        family_name: "",    //员工姓
        preferred_name: "",    //员工姓名
        staff_email: "",    //邮箱
        is_dept_manage: 0,        //	是否部门负责人 1是 0否
        is_org_manager: 0,        //	是否单位负责人 1是 0否
        direct_leader_code: '',    //	直属领导
        staff_image: '展示不同自己添加',	//头像
        staff_status: 1
      },
      files: [],
      files2: {}
    };
  },
  validations: {
    stafflist: {
      preferred_name: {
        required
      },
      family_name: {
        required
      },
      child_name: {

      },
      show_name: {
        required
      },
      staff_email: {
        required,
        email
      },
      staff_code: {
        required
      },
      dept_uuid: {
        required
      },
      job_post_uuid: {
        required
      },
      job_post_grade_uuid: {
        required
      },
      is_org_manager: {

      },
      is_dept_manage: {

      },
      direct_leader_code: {

      },
      role_uuids: {
        required
      },
      first_employment_date: {
        required,
        beforeDate (value) {
          if (!this.stafflist.probation_end_date) return true
          const prevData = new Date(value).valueOf()
          const nextData = new Date(this.stafflist.probation_end_date).valueOf()
          return prevData < nextData
        }
      },
      probation_end_date: {
        required,
        afterDate (value) {
          if (!this.stafflist.first_employment_date) return true
          const nextData = new Date(value).valueOf()
          const prevData = new Date(this.stafflist.first_employment_date).valueOf()
          return prevData < nextData
        }
      },
    }
  },
  created () { },
  async mounted () {
    await this.national('mrbase_sys_staffs')
  },
  methods: {
    changeimgSrc () {

    },
    cancel () {

    },
    success () {
      this.$v.stafflist.$touch()
      if (this.$v.stafflist.$error) return
      this.stafflist.org_level_uuid = this.company_uuid
      // Array.isArray(this.stafflist.role_uuids) ? this.stafflist.role_uuids = this.stafflist.role_uuids.join() : ''
      // this.stafflist.role_uuids = this.roleUuids.join()
      // this.stafflist.job_post_grade_uuid = Array.isArray(this.stafflist.job_post_grade_uuid) ? this.stafflist.job_post_grade_uuid.join() : this.stafflist.job_post_grade_uuid
      this.$emit('changee1', 2, this.stafflist)
    }
  },
  computed: {
    job_post_grade_uuid: {
      get () {
        if (this.stafflist.job_post_grade_uuid.length > 0) {
          if (this.stafflist.job_post_grade_uuid.length > 1) {
            return this.stafflist.job_post_grade_uuid.split(',')
          }
          return [this.stafflist.job_post_grade_uuid]

        } else {
          return []
        }
      },
      set (val) {
        this.stafflist.job_post_grade_uuid = val.join()
      }
    },
    roleUuids: {
      get () {
        if (this.stafflist.role_uuids.length > 0) {
          if (this.stafflist.role_uuids.length > 1) {
            return this.stafflist.role_uuids.split(',')
          }
          return [this.stafflist.role_uuids]

        } else {
          return []
        }
      },
      set (val) {
        this.stafflist.role_uuids = val.join()
      }
    },
    company_uuid () { //当前选中公司
      return this.$store.state.currentCompany
    },
    companyName () {  //部门参照查询字段
      return { prompt_name: 'levelUuid_deptUuid', org_level_uuid: this.company_uuid }
    },
    job_post_uuid () {  //岗位查询
      return { prompt_name: 'level-dept-post', dept_uuid: this.stafflist.dept_uuid }
    },
    superior () { //上司高级参照
      return { prompt_name: 'new_boss', org_level_uuid: this.company_uuid }
    },
    spica () { //角色高级参照 
      return { prompt_name: 'level-role', org_level_uuid: this.company_uuid }
    },
    mrbase_sys_job_post_grades () {
      return { prompt_name: 'mrbase_sys_job_post_grades', org_level_uuid: this.company_uuid }
    }
  },
  watch: {},
};
</script>

<style scoped>
</style>
